@font-face {
  font-family: "ReciaSerifDisplay";
  font-weight: normal;
  font-style: normal;
  src: url("https://cdn.jsdelivr.net/gh/webfontworld/recia/ReciaSerifDisplay.eot");
  src: url("https://cdn.jsdelivr.net/gh/webfontworld/recia/ReciaSerifDisplay.eot?#iefix")
      format("embedded-opentype"),
    url("https://cdn.jsdelivr.net/gh/webfontworld/recia/ReciaSerifDisplay.woff2")
      format("woff2"),
    url("https://cdn.jsdelivr.net/gh/webfontworld/recia/ReciaSerifDisplay.woff")
      format("woff"),
    url("https://cdn.jsdelivr.net/gh/webfontworld/recia/ReciaSerifDisplay.ttf")
      format("truetype");
  font-display: swap;
}
@font-face {
  font-family: "IMHyemin";
  font-weight: 400;
  font-style: normal;
  src: url("https://cdn.jsdelivr.net/gh/webfontworld/dgb/IMHyeminRegular.eot");
  src: url("https://cdn.jsdelivr.net/gh/webfontworld/dgb/IMHyeminRegular.eot?#iefix")
      format("embedded-opentype"),
    url("https://cdn.jsdelivr.net/gh/webfontworld/dgb/IMHyeminRegular.woff2")
      format("woff2"),
    url("https://cdn.jsdelivr.net/gh/webfontworld/dgb/IMHyeminRegular.woff")
      format("woff"),
    url("https://cdn.jsdelivr.net/gh/webfontworld/dgb/IMHyeminRegular.ttf")
      format("truetype");
  font-display: swap;
}
@font-face {
  font-family: "IMHyemin";
  font-weight: 700;
  font-style: normal;
  src: url("https://cdn.jsdelivr.net/gh/webfontworld/dgb/IMHyeminBold.eot");
  src: url("https://cdn.jsdelivr.net/gh/webfontworld/dgb/IMHyeminBold.eot?#iefix")
      format("embedded-opentype"),
    url("https://cdn.jsdelivr.net/gh/webfontworld/dgb/IMHyeminBold.woff2")
      format("woff2"),
    url("https://cdn.jsdelivr.net/gh/webfontworld/dgb/IMHyeminBold.woff")
      format("woff"),
    url("https://cdn.jsdelivr.net/gh/webfontworld/dgb/IMHyeminBold.ttf")
      format("truetype");
  font-display: swap;
}

:root {
  --primary-100: #8fbf9f;
  --primary-200: #68a67d;
  --primary-300: #24613b;
  --accent-100: #f18f01;
  --accent-200: #833500;
  --text-100: #353535;
  --text-200: #5f5f5f;
  --bg-100: #f5ecd7;
  --bg-200: #ebe2cd;
  --bg-300: #c2baa6;
}

.App {
  background-color: var(--bg-100);
}

@mixin reset {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "IMHyemin";
  font-weight: 700;
  color: var(--accent-200);
}

* {
  @include reset;
  &:before,
  &:after {
    @include reset;
  }
}

ul {
  list-style-type: none;
}

a {
  text-decoration: none;
}

.undefined {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 2rem;
  height: 50vh;
}

/* header */
.brand {
  font-family: "ReciaSerifDisplay";
  font-size: 3rem;
  text-align: center;
  padding: 1rem 0;
}
.menu {
  display: none;
}

nav img {
  width: 100vw;
  height: 300px;
  object-fit: cover;
}
nav .menu {
  span {
    position: absolute;
    top: 30px;
    right: 20px;
    background-color: var(--accent-200);
    width: 40px;
    height: 3px;
    transition: 0.35s;
    &:nth-of-type(1) {
      &.show {
        transform: rotate(-45deg);
      }
    }
    &:nth-of-type(2) {
      top: 40px;
      &.show {
        display: none;
      }
    }
    &:nth-of-type(3) {
      top: 50px;
      &.show {
        transform: rotate(45deg);
      }
    }
  }
}

.login,
.cart,
.recent {
  font-size: 1.3rem;
  position: absolute;
  top: 1.5rem;
  right: 2rem;
}

.recent {
  right: 5.5rem;
}

.loading {
  text-align: center;
  font-size: 2.5rem;
  font-weight: 700;
  margin: 4rem 0;
  font-family: "ReciaSerifDisplay";
  letter-spacing: 0.5rem;
}

h2 {
  text-align: center;
  font-size: 2rem;
  font-family: "ReciaSerifDisplay";
  letter-spacing: 0.5rem;
}

/* item */
.items {
  text-align: center;
  .list {
    text-align: center;
    padding-top: 2rem;
    gap: 2rem;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    font-size: 1.5rem;
    .img {
      width: 50%;
      height: 400px;
      object-fit: cover;
      cursor: pointer;
      border: 2px solid transparent;
      transition: 0.35s;
      border-radius: 20px;
      &:hover {
        border-color: var(--primary-100);
      }
    }
    .name {
      cursor: pointer;
    }
    .price {
      font-size: 1rem;
      margin: 0.5rem 0;
    }
  }
  .more {
    width: 10rem;
    margin: 5rem 0;
    font-family: "ReciaSerifDisplay";
    letter-spacing: 0.3rem;
    padding: 0.5rem;
    font-size: 2rem;
    background-color: var(--primary-300);
    color: var(--bg-100);
    border: 1px solid transparent;
    outline: none;
    border-radius: 0.5rem;
    cursor: pointer;
    transition: 0.35s;
    &:hover {
      color: var(--primary-300);
      background-color: var(--bg-100);
      border-color: var(--primary-300);
    }
  }
}

/* detail */
.start {
  opacity: 0;
}
.end {
  opacity: 1;
  transition: opacity 0.5s;
}
.detail {
  display: flex;
  margin-top: 100px;
  .detail-box {
    width: 50vw;
    height: 800px;
    overflow: hidden;
    text-align: center;
    .detail-img {
      width: 50%;
      border-radius: 5%;
    }
    .detail-title {
      display: none;
    }
  }
  .order {
    width: 50vw;
    height: 800px;
    padding-right: 10vw;
    padding-top: 3vw;
    .detail-name {
      font-size: 2rem;
      padding-bottom: 1.5rem;
      border-bottom: 2px solid var(--accent-200);
    }
    .detail-price {
      display: block;
      margin-top: 1vw;
      font-size: 1.5rem;
      line-height: 1.5;
      text-align: right;
    }
    .detail-info {
      margin-top: 3vw;
      font-size: 1.3rem;
      font-weight: 400;
      line-height: 1.5;
      width: 30vw;
    }
    button {
      width: 100%;
      margin: 5rem 0;
      font-family: "ReciaSerifDisplay";
      letter-spacing: 0.3rem;
      padding: 0.7rem;
      font-size: 2rem;
      background-color: var(--primary-300);
      color: var(--bg-100);
      border: 1px solid transparent;
      outline: none;
      border-radius: 0.5rem;
      cursor: pointer;
      transition: 0.35s;
      &:hover {
        color: var(--primary-300);
        background-color: var(--bg-100);
        border-color: var(--primary-300);
      }
    }
  }
}
.event {
  background-color: var(--bg-300);
  height: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  span {
    background-color: var(--bg-300);
    font-size: 2rem;
  }
}

/* cart */
table {
  width: 90%;
  margin: auto;
  font-size: 1.3rem;
  text-align: center;
  padding: 3rem 0;
  line-height: 2;
  height: 50vh;
  thead {
    tr {
      th {
        border-bottom: 2px solid var(--accent-200);
      }
    }
  }
  tbody {
    td {
      border-bottom: 1px solid var(--accent-200);
    }
  }
  button {
    background-color: var(--primary-200);
    border: 1px solid var(--primary-300);
    outline: none;
    padding: 0.5rem;
    margin: 1rem;
    font-size: 1.2rem;
    font-weight: 400;
    cursor: pointer;
  }
}

/* footer */
footer {
  width: 100%;
  height: 100px;
  text-align: center;
  address {
    line-height: 2;
    margin-top: 50px;
    font-style: normal;
    font-weight: 400;
  }
}

@media all and (max-width: 1023px) {
  .items .list {
    grid-template-columns: repeat(2, 1fr);
    .img {
      width: 60%;
      height: 300px;
    }
  }
}
@media all and (max-width: 767px) {
  .brand {
    font-size: 2rem;
  }
  .login {
    display: none;
  }
  .cart {
    display: none;
  }
  nav .menu {
    display: inline-block;
    width: 40px;
    height: 40px;
    position: absolute;
    top: 1rem;
    right: 1rem;
  }
  .items .list {
    grid-template-columns: repeat(1, 1fr);
    .img {
      width: 60%;
      height: 300px;
    }
  }
  .detail {
    display: block;
    margin-top: 0;
  }
  .detail .detail-box {
    width: 100vw;
    height: 600px;
    padding: 0;
  }
  .detail .detail-box .detail-title {
    display: block;
    font-size: 2rem;
    padding: 1.5rem 0;
  }
  .detail .detail-box .detail-img {
    width: 100%;
    border-radius: 0;
  }
  .detail .order {
    width: 100vw;
    height: 500px;
    padding: 0;
  }
  .detail .order .detail-name {
    display: none;
  }
  .detail .order .detail-info {
    width: 100%;
    padding: 1rem;
    margin: 0;
  }
  .detail .order .detail-price {
    text-align: center;
  }
  .detail .order button {
    width: 90%;
    margin: 1rem;
  }
  .gnb {
    position: absolute;
    left: calc(-100vw + 3rem);
    width: calc(100vw + 0.5rem);
    background-color: var(--bg-100);
    font-size: 2rem;
    line-height: 2;
    text-align: right;
    padding: 1rem;
    padding-right: 3rem;
  }
  .recent {
    display: none;
  }
  .cart-img {
    display: none;
  }
}
